/* make variables */
:root {
  --lightgray: #D9D9D9;
  --gray: #157F1F ;
  --orange: #C7EF00;
  --darkGrey: #02550a;
  --caloryCard: #656565;
  /* --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%); */
  --planCard: linear-gradient(200.41deg, #9dbd00  20.14%, #157F1F 80.75%);
  --appColor:  #000000;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}
.stroke-text{
  font-weight: bold;
  color: transparent;
  font-size: 3rem;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: var(--orange);
}
.btn{
  background-color: white;
  padding: 0.5rem;
  font-weight: bold;
  border: 4px solid transparent;
  transition: 300ms all;
  align-items: center;
  justify-content: center;
}
.btn:hover{
  cursor: pointer;
}
.blur{
  background-color: #157F1F;
  position: absolute;
  border-radius: 50%;
  filter: blur(200px);
  z-index: -9;
}